.schedule-section {
  padding: 74px 0 96px; }

.schedule {
  &__header {
    @extend %container;
    flex-direction: column; }
  &__title {
    font-size: 5rem;
    font-weight: bold;
    line-height: 1.14;
    padding-top: 18px;
    @include mediaSize($tablet) {
      font-size: 5.6rem; } }
  &__cards {
    position: relative;
    padding-top: 52px; }
  &__circle {
    position: absolute;
    top: 10px;
    left: -65px;
    z-index: -1; }
  &__card {
    display: flex;
    flex-direction: column;
    background-color: $gray2;
    border-radius: 20px;
    margin-bottom: 32px;
    &:nth-last-child(1) {
      margin-bottom: 0; }
    @include mediaSize($phone) {
      flex-direction: row; } }
  &__tipe {
    display: flex;
    flex-direction: column;
    @include mediaSize($phone) {
      width: 50%; }
    @include mediaSize($tablet) {
      padding: 40px; }
    @include mediaSize($notebook) {
      padding: 0;
      flex-direction: row; } }
  &__img {
    object-fit: cover;
    object-position: center;
    img {
      border-radius: 20px;
      width: 100%; }
    @include mediaSize($phone) {
      width: 270px;
      height: 160px; } }
  &__discription {
    padding: 37px 0 0 30px; }
  &__alert {
    display: inline-block; }
  &__h3 {
    font-size: 2.4rem;
    font-weight: bold;
    line-height: 1.33;
    padding-top: 20px; }
  &__values {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    padding: 0 0 50px 30px;
    @include mediaSize($phone) {
      padding: 0;
      width: 60%;
      align-items: center; }
    @include mediaSize($tablet) {
      flex-direction: row;
      width: 50%; } }
  &__data {
    @include mediaSize($tablet) {
      padding: 47px 0 38px; } }
  &__name {
    font-size: 2.4rem;
    line-height: 1.71;
    color: $gray5;
    padding-top: 19px;
    @include mediaSize($phone) {
      padding-top: 0; } }
  &__value {
    font-size: 1.8rem;
    @include mediaSize($phone) {
      padding-top: 19px;
      line-height: 1.78px; } } }
