.abouts {
  background-color: $gray2;
  padding: 50px 0;
  @include mediaSize($tablet) {
    background: linear-gradient(to left, $gray2 80%, $white 20%);
    padding: 160px 0; } }

.circle-pink_about {
  position: absolute;
  top: 2.5rem;
  right: -1rem;
  display: none;
  @include mediaSize($projector) {
    display: block; } }

.about__name-phone {
  margin: 0 auto;
  font-size: 1.6rem;
  color: $black;
  font-weight: 700;
  line-height: 2;
  text-align: center;
  @include mediaSize($tablet) {
    display: none; } }

.about {
  @extend %container;
  position: relative;
  flex-wrap: wrap;
  justify-content: center;
  @include mediaSize($tablet) {
    flex-wrap: nowrap;
    justify-content: space-between; }
  &__nameForTablet {
    display: none;
    @include mediaSize($tablet) {
      display: block; } }
  &__img {
    width: 300px;
    height: 560px;
    border-radius: 40px;
    margin-top: 50px;
    background-position: center;
    @include mediaSize($phone) {
      width: 100%; }
    @include mediaSize($tablet) {
      width: 400px;
      margin-top: 0; }
    @include mediaSize($notebook) {
      width: 500px; }
    @include mediaSize($desktop) {
      width: 540px; }
    @include mediaSize($projector) {
      width: 570px; } }
  &__content {
    width: 300px;
    text-align: center;
    padding: 0 15px;
    z-index: 10;
    @include mediaSize($phone) {
      width: auto; }
    @include mediaSize($tablet) {
      width: 40%; }
    @include mediaSize($notebook) {
      width: 50%;
      text-align: left;
      padding-left: 30px; }
    @include mediaSize($projector) {
      padding-left: 130px; } }
  &__title {
    font-size: 4rem;
    font-weight: 700;
    line-height: 1.14;
    letter-spacing: 1.4px;
    padding: 18px 0 26px;
    @include mediaSize($phone) {
      font-size: 5rem; }
    @include mediaSize($notebook) {
      font-size: 5.6rem;
      width: 450px; } }
  &__slogan {
    font-size: 2.2rem;
    line-height: 1.45;
    @include mediaSize($tablet); }
  &__txt {
    font-size: 1.8rem;
    line-height: 1.78;
    color: $gray5;
    padding: 34px 0 48px; } }
