.header-site {
  position: relative;
  width: 100%;
  background-color: $violet;
  padding: 3.2rem 0 4.8rem;
  &__menu {
    display: none;
    flex-direction: row-reverse;
    justify-content: space-between;
    position: fixed;
    z-index: 100;
    right: 0;
    top: 0;
    height: 100%;
    width: 100%;
    overflow: auto;
    background-color: $transparent; }
  &__menu-panel {
    background-color: $black;
    height: 100%;
    padding-top: 1.5rem; }
  &__close-menu {
    font-size: 25px;
    font-weight: 700;
    color: $white;
    cursor: pointer;
    outline: 0;
    padding-left: 1.5rem; }
  &__items {
    padding: 5rem; }
  &__item {
    height: 6rem;
    padding-top: 1rem;
    &:hover .header-site__link {
      text-decoration: underline; } }
  &__link {
    color: $white;
    text-decoration: none;
    font-size: 2rem;
    @include mediaSize($phone) {
      font-size: 3rem; }
    @include mediaSize($notebook) {
      font-size: 4rem; } } }


.menu-site {
  @extend %container;
  @include mediaSize($phoneVertical) {
    flex-wrap: wrap; }
  @include mediaSize($tablet) {
    flex-wrap: nowrap; } }

.logo {
  color: $white;
  line-height: 1.2;
  text-decoration: none;
  @include mediaSize($phoneVertical) {
    font-size: 3rem;
    font-weight: 500; }
  @include mediaSize($notebook) {
    font-size: 4rem;
    font-weight: 700; } }

.search-form {
  @extend %container;
  width: 568px;
  height: 46px;
  background-color: $white;
  border-radius: 24px;
  border: 1px solid $grayBorder;
  padding-left: 2.4rem;
  overflow: hidden;
  margin: 2rem 0;
  order: 1;
  @include mediaSize($tablet) {
    margin-bottom: 0;
    order: 0; }
  &__input {
    width: 50rem;
    height: 3.2rem;
    border: none; } }

.search-form_txt {
  color: $gray5;
  font-size: 1.8rem;
  line-height: 1.78;
  font-weight: 400; }
