.activeCourses {
  color: $violet; }

.slider-header {
  width: 100%;
  background-color: $violet;
  @include mediaSize($notebook) {
    padding-bottom: 9.6rem; } }

.slider {
  display: flex;
  justify-content: flex-start;
  flex-direction: column-reverse;
  @include mediaSize($notebook) {
    flex-direction: row; }
  &__panel {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    @include mediaSize($notebook) {
      flex-direction: column;
      padding: 8.8rem 0 8rem; } }
  &__btn {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-direction: row;
    width: 11.2rem;
    margin: 5rem auto;
    @include mediaSize($notebook) {
      flex-direction: column;
      height: 11.2rem;
      width: auto;
      margin: 0; } }
  &__img {
    height: 65.6rem;
    border-radius: 74px;
    z-index: 10;
    background-position: center;
    @include mediaSize($phoneVertical) {
      display: none;
      width: 300px;
      padding: 6.4rem 1rem 6.4rem; }
    @include mediaSize($phone) {
      width: 550px;
      padding: 6.4rem 6.8rem 6.4rem 10rem; }
    @include mediaSize($tablet) {
      width: 750px; }
    @include mediaSize($notebook) {
      width: 900px;
      margin-left: -80px; }
    @include mediaSize($desktop) {
      width: 97rem; } }
  &__info-box {
    @extend %container; }
  &__title {
    color: $white;
    margin-top: 12.6rem;
    @extend .slide-left;
    @include mediaSize($phoneVertical) {
      font-size: 3.7rem;
      font-weight: 500; }
    @include mediaSize($phone) {
      font-size: 4.7rem;
      line-height: 1.11; }
    @include mediaSize($tablet) {
      width: 600px;
      font-size: 5.7rem;
      font-weight: 700; }
    @include mediaSize($notebook) {
      width: 670px;
      font-size: 7.2rem; } }
  &__author {
    color: $white;
    font-size: 2.2rem;
    line-height: 1.45;
    padding: 2.6rem 0 7.2rem; }
  &__footer {
    @extend %container;
    margin-top: 2.6rem; } }
