.subject {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  background-color: $gray2;
  border-radius: 2rem;
  overflow: hidden;
  position: relative;
  margin-bottom: 32px;
  &:nth-child(n+7) {
    display: none; }
  @extend .scale-up-ver-center;
  @include mediaSize($phone) {
    width: 80%; }
  @include mediaSize($tablet) {
    width: auto;
    max-width: 370px;
    margin-right: 5px; }
  @include mediaSize($notebook) {
    margin: 0 32px 32px 0; }
  @include mediaSize($desktop);
  @include mediaSize($projector) {
    width: 1206px; }
  .alert {
    position: absolute;
    top: 3.6rem;
    left: 3.2rem; }
  &__image {
    width: 100%;
    height: 304px;
    border-radius: 2rem;
    overflow: hidden;
    @extend .scale-up-ver-center;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  &__content {
    padding: 2.3rem 3.2rem 3.2rem; }
  &__author {
    font-size: 1.8rem;
    line-height: 1.78;
    color: $black;
    padding: 0.3rem 0 0.6rem; }
  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  &__lessons {
    @extend %textСomplementary; } }

.expert {
  max-width: 250px;
  background-color: $gray2;
  border-radius: 2rem;
  margin: 112px 0 50px 52px;
  &:nth-child(n+5) {
    display: none; }
  @include mediaSize($phone) {
    max-width: 270px; }
  @include mediaSize($projector) {
    margin: 112px 0 60px 52px;
    &:nth-last-child(-n+3) {
      margin-bottom: 0; } }
  &__photo {
    width: 27rem;
    height: 27rem;
    position: relative;
    top: -112px;
    left: -52px;
    margin-bottom: -112px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  &__about {
    padding: 2.5rem 3.2rem 4.8rem; }
  &__prof {
    @extend %textСomplementary; }
  &__social {
    display: flex;
    padding-top: 22px;
    .button {
      margin-right: 0.8rem;
      color: $violet; } } }
