// TTITLE animation
.slide-left {
  animation-name: slide-left;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both; }

@keyframes slide-left {
  0% {
    transform: translateX(150px); }

  100% {
    transform: translateX(0px); } }

// MENU animation
.slide-menu {
  animation-name: slide-menu;
  animation-duration: .3s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both; }

@keyframes slide-menu {
  0% {
    transform: translateX(150px); }

  100% {
    transform: translateX(0px); } }

// CARDS animation
.scale-up-ver-center {
	animation: scale-up-ver-center 0.8s cubic-bezier(0.390, 0.575, 0.565, 1.000) both; }

@keyframes scale-up-ver-center {
  0% {
    transform: scaleY(0.4); }
  100% {
    transform: scaleY(1); } }

// CARDS TESTIMONIALS
.shadow-drop-2-lr {
  animation: shadow-drop-2-lr 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both; }

@keyframes shadow-drop-2-lr {
  0% {
    transform: translateZ(0);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0), 0 0 0 0 rgba(0, 0, 0, 0); }
  100% {
    transform: translateZ(50px);
    box-shadow: -12px 0 30px -12px rgba(0, 0, 0, 0.35), 12px 0 30px -12px rgba(0, 0, 0, 0.35); } }

// FOR icon
.flip-scale-2-ver-right {
  animation: flip-scale-2-ver-right 0.8s linear both; }

@keyframes flip-scale-2-ver-right {
  0% {
    transform: translateX(0) rotateY(0) scale(1);
    transform-origin: 100% 50%; }
  50% {
    transform: translateX(50%) rotateY(-90deg) scale(2);
    transform-origin: 50% 50%; }
  100% {
    transform: translateX(100%) rotateY(-180deg) scale(1);
    transform-origin: 0% 50%; } }

// For CLOCK
.scale-down-center {
	animation: scale-down-center 1s infinite cubic-bezier(0.250, 0.460, 0.450, 0.940) both; }

@keyframes scale-down-center {
  0% {
    transform: scale(0.8); }
  100% {
    transform: scale(1); } }
