.stocks {
  background-color: $gray2;
  padding: 100px 0;
  @include mediaSize($tablet) {
    background: linear-gradient(to right, $gray2 80%, $white 20%);
    padding: 160px 0; } }

.circle-pink_right {
  position: absolute;
  top: 12rem;
  right: -7rem;
  display: none;
  @include mediaSize($projector) {
    display: block; } }

.stock {
  @extend %container;
  position: relative;
  flex-wrap: wrap;
  justify-content: center;
  @include mediaSize($tablet) {
    flex-wrap: nowrap;
    justify-content: space-between; }
  &__info {
    width: 300px;
    text-align: center;
    @include mediaSize($phone) {
      width: auto; }
    @include mediaSize($tablet) {
      width: 40%;
      text-align: justify; }
    @include mediaSize($notebook) {
      width: 50%;
      text-align: left; } }
  &__title {
    font-size: 5rem;
    font-weight: 700;
    line-height: 1.14;
    letter-spacing: 1.4px;
    padding: 18px 0 26px;
    @include mediaSize($tablet) {
      font-size: 5.6rem; } }
  &__slogan {
    font-size: 2.2rem;
    line-height: 1.45; }
  &__txt {
    font-size: 1.8rem;
    line-height: 1.78;
    color: $gray5;
    padding: 34px 0 48px;
    @include mediaSize($notebook) {
      width: 47rem; } } }

.timer {
  @extend %container;
  justify-content: center;
  @include mediaSize($tablet) {
    width: 300px;
    justify-content: space-between; }
  @include mediaSize($notebook) {
    width: 370px; }
  &__clock {
    width: 50px;
    font-size: 4rem;
    font-weight: 700;
    line-height: 1.2;
    // margin-right: 15px
    @include mediaSize($phone) {
 } }      // margin-right: 23px
  &__name {
    font-size: 1.4rem;
    font-weight: normal;
    line-height: 1.71;
    color: $gray5; }
  &__timeOut {
    display: flex;
    justify-content: space-between; } }

.progress-bar {
  height: .8rem;
  border-radius: 4px;
  margin-top: 26px;
  @include mediaSize($phone) {
    width: 34.6rem;
    margin-left: 100px; }
  @include mediaSize($tablet) {
    margin-left: 0; }
  &__fill {
    height: 100%;
    border-radius: 4px;
    background-color: $green; } }

.subscribtion {
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 560px;
  border-radius: 40px;
  background-color: $violet;
  margin-top: 50px;
  padding: 8.1rem 4.8rem 4.8rem;
  z-index: 10;
  @include mediaSize($phone) {
    width: 500px; }
  @include mediaSize($tablet) {
    width: 400px;
    margin-top: 0; }
  @include mediaSize($notebook) {
    width: 500px; }
  @include mediaSize($desktop) {
    width: 540px; }
  @include mediaSize($projector) {
    width: 570px; }
  &__input {
    height: 4.8rem;
    border-radius: 24px;
    border: 1px solid $grayBorder;
    background-color: $white;
    padding-left: 2.4rem;
    margin-bottom: 3.2rem;
    font-size: 1.8rem;
    line-height: 1.78;
    color: $gray5; }
  &__input_textarea {
    resize: none;
    padding-top: .8rem; }
  &__button {
    display: flex;
    justify-content: flex-end; } }
