.fa-chalkboard-teacher {
  font-size: 4rem;
  color: $violet; }

.fa-leanpub {
  font-size: 5rem;
  color: $rose; }

.fas.fa-flask {
  font-size: 4rem;
  color: $violetLight; }

.fa-long-arrow-alt-right {
  margin-left: 15px; }

.features-cards-section {
  padding: 30px 0 86px;
  @include mediaSize($notebook) {
    padding: 80px 0 86px; } }

.features-cards {
  @extend %container;
  flex-wrap: wrap;
  padding: 0 30px 0 70px;
  @include mediaSize($notebook) {
    padding: 0 20px 0 30px; }
  @include mediaSize($desktop) {
    padding-left: 100px; } }

.features-card {
  width: 270px;
  margin-top: 50px;
  @include mediaSize($notebook) {
    margin-top: 0; }
  &__pic {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 96px;
    height: 96px;
    border-radius: 50%;
    background-color: $gray2; }
  &__title {
    font-size: 2.4rem;
    font-weight: 500;
    line-height: 1.33;
    padding: 23px 0 19px; }
  &__txt {
    font-size: 1.8rem;
    line-height: 1.78;
    color: $gray5; }
  &__footer {
    padding-top: 16px; }
  &__link {
    font-size: 1.8rem;
    line-height: 1.78;
    text-decoration: none;
    color: $black;
    &:hover {
      color: $violet; } } }
