@import "reset";
@import "vars";
@import "mixins";
@import "media";
@import "config";
@import "blocks/all_blocks";

.wrapper {
  @extend %wrapper; }

.active {
  display: block; }

.activeFlex {
  display: flex; }

.hide {
  display: none; }

.circles-blue {
  background-image: url(../../img/circlepng.png);
  display: none;
  @include mediaSize($notebook) {
    display: block;
    width: 176px;
    height: 176px; } }

.circles-blue_right {
  margin: 37.1rem 0 0 -15.7rem;
  @include mediaSize($phoneVertical) {
    display: none; }
  @include mediaSize($desktop) {
    display: block; } }

.circle-pink {
  background-image: url(../../img/circle_pink.png);
  width: 176px;
  height: 176px; }

.social-bar {
  @extend %container;
  width: 160px; }

.active-link {
  color: $violet; }


.bc-violet {
  background-color: $violet; }

.bc-blue {
  background-color: $blue; }

.bc-rose {
  background-color: $rose; }

.bc-violetLight {
  background-color: $violetLight; }

.bc-green {
  background-color: $green; }

.bc-btn {
  background-color: $grayHover2;
  &:hover {
    background-color: $grayHover; } }

.animaton_sec {
  @extend .scale-down-center;
  font-weight: normal; }

.name-section_white {
  color: $white; }

.clock-width {
  width: 50px;
  text-align: center; }
