.teachers-section {
  padding: 74px 0 64px; }

.teachers {
  &__header {
    @extend %container;
    flex-direction: column; }
  &__title {
    font-size: 5rem;
    font-weight: bold;
    line-height: 1.14;
    padding-top: 18px;
    @include mediaSize($tablet) {
      font-size: 5.6rem; } }
  &__cards {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    position: relative;
    padding-top: 52px;
    @include mediaSize($desktop) {
      justify-content: space-between;
      padding: 52px 10px 48px 5px; }
    @include mediaSize($projector) {
      padding: 52px 50px 48px 0; } }
  &__circles {
    position: absolute;
    left: -20px;
    top: 300px;
    z-index: -1; } }
