.gallery-section {
  position: relative;
  padding: 6rem 0;
  @include mediaSize($phone) {
    padding: 74px 0 144px; } }

.gallery {
  &__header {
    @extend %container;
    flex-direction: column; }
  &__title {
    font-size: 5rem;
    font-weight: bold;
    line-height: 1.14;
    padding-top: 18px;
    @include mediaSize($tablet) {
      font-size: 5.6rem; } }
  &__content {
    @extend %container;
    padding-top: 68px;
    flex-direction: column;
    @include mediaSize($projector) {
      flex-direction: row; } }
  &__modalBox {
    display: none;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: $gray8; }
  &__close-modalBox {
    position: absolute;
    top: 14%;
    right: 14%;
    font-size: 25px;
    font-weight: bold;
    color: $white;
    cursor: pointer;
    outline: 0; }
  &__clip {
    display: block;
    width: 960px;
    height: 540px;
    border: 0;
    max-width: 100%;
    margin: 10% auto; }
  &__poster-video {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 480px;
    border-radius: 20px;
    background-position: center;
    @include mediaSize($phone) {
      width: 570px; } }
  &__photo {
    @extend %container;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    @include mediaSize($phone) {
      width: 570px;
      height: 480px;
      padding-top: 160px; }
    img {
      border-radius: 20px;
      cursor: pointer; }
    a {
      margin: 32px 0 0 15px;
      outline: 0;
      display: none;
      &:first-child {
        display: block; }
      &:nth-last-child(-n+2) {
        margin-bottom: 0; }
      @include mediaSize($phone) {
        margin: 0 0 32px 0;
        display: block; } }
    @include mediaSize($projector) {
      padding-top: 0; } }
  &__circles {
    position: absolute;
    left: 34px;
    top: 340px;
    z-index: -1; } }
