.fa-headset {
  color: $violet;
  font-size: 5rem; }

.fa-gift {
  color: $blue;
  font-size: 5rem; }

.features-section {
  background-color: $gray2;
  padding-top: 60px;
  @include mediaSize($tablet) {
    background: linear-gradient(to right, $gray2 80%, $white 20%); }
  @include mediaSize($notebook) {
    padding: 160px 0; } }

.features {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column-reverse;
  @include mediaSize($tablet) {
    flex-wrap: nowrap;
    justify-content: space-between;
    flex-direction: row; }
  &__info {
    width: 300px;
    text-align: center;
    @include mediaSize($phone) {
      width: auto; }
    @include mediaSize($tablet) {
      width: 40%;
      text-align: justify; }
    @include mediaSize($notebook) {
      width: 50%;
      text-align: left; } }
  &__title {
    font-size: 5rem;
    font-weight: 700;
    line-height: 1.14;
    letter-spacing: 1.4px;
    padding: 18px 0 26px;
    @include mediaSize($tablet) {
      font-size: 5.6rem; } }
  &__slogan {
    font-size: 2.2rem;
    line-height: 1.45; }
  &__txt {
    font-size: 1.8rem;
    line-height: 1.78;
    color: $gray5;
    padding: 34px 0 48px;
    @include mediaSize($notebook) {
      width: 47rem; } }
  &__img {
    display: flex;
    flex-direction: column;
    width: 300px;
    height: 560px;
    border-radius: 40px;
    background-position: center;
    margin-top: 50px;
    padding: 8.1rem 4.8rem 4.8rem;
    z-index: 10;
    @include mediaSize($phone) {
      width: 100%; }
    @include mediaSize($tablet) {
      width: 400px;
      margin-top: 0; }
    @include mediaSize($notebook) {
      width: 500px; }
    @include mediaSize($desktop) {
      width: 540px; }
    @include mediaSize($projector) {
      width: 570px; } }
  &__cards {
    display: flex;
    align-items: center;
    flex-direction: column-reverse;
    margin-bottom: 48px;
    @include mediaSize($notebook) {
      flex-direction: row; } }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 96px;
    height: 96px;
    border-radius: 50%;
    background-color: $white; }
  &__card {
    padding-left: 28px;
    @include mediaSize($notebook) {
      width: 382px; } }
  &__card-title {
    font-size: 2.4rem;
    line-height: 1.33;
    font-weight: bold;
    padding-bottom: 3px; }
  &__card-txt {
    font-size: 1.8rem;
    line-height: 1.78;
    color: $gray5; } }
