.footer-site {
  background-color: $violet;
  padding: 74px 0 38px;
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-bottom: 1px solid $gray6;
    padding-bottom: 65px;
    @include mediaSize($notebook) {
      flex-direction: row;
      justify-content: flex-start;
      text-align: left; } }
  &__contacts {
    @include mediaSize($phone) {
      width: 370px; } }
  &__txt {
    font-size: 1.8rem;
    line-height: 1.78;
    color: $gray7;
    padding: 17px 0; }
  &__popular {
    margin-top: 60px;
    @include mediaSize($notebook) {
      margin-top: 0;
      padding-left: 26px; } }
  &__popular-link {
    display: block;
    color: $white;
    text-decoration: none;
    font-size: 1.8rem;
    line-height: 1.78;
    padding-top: 16px; }
  &__news {
    margin-top: 60px;
    @include mediaSize($notebook) {
      margin-top: 0;
      padding-left: 143px; } }
  &__newsletter {
    display: flex;
    flex-direction: column; }
  &__newsletter_input {
    margin: 22px 0 16px;
    @include mediaSize($phone) {
      width: 370px; } }
  &__btn {
    display: flex; }
  &__footer {
    @extend %container;
    flex-direction: column;
    @include mediaSize($tablet) {
      flex-direction: row; } }
  &__copyright {
    padding-top: 23px; }
  &__links {
    display: flex;
    justify-content: space-between;
    width: 310px; } }

.ml-100 {
  margin-left: 70px;
  @include mediaSize($phone) {
    margin-left: 100px; }
  @include mediaSize($notebook) {
    margin-left: 0; } }
