.fa-react {
  font-size: 5rem;
  color: $white; }

.fa-user-graduate,
.fa-photo-video,
.fa-school {
  font-size: 4rem;
  color: $white; }

.statistic-section {
  padding: 30px 0 86px;
  background-color: $violet;
  @include mediaSize($notebook) {
    padding: 96px 0 120px; } }

.statistics {
  @extend %container;
  flex-wrap: wrap;
  flex-direction: column;
  @include mediaSize($tablet) {
    flex-direction: row;
    padding: 0 40px; }
  @include mediaSize($projector) {
    padding-right: 160px; } }

.statistic {
  margin-top: 50px;
  @include mediaSize($notebook) {
    margin-top: 0; }
  &__pic {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 96px;
    height: 96px;
    border-radius: 50%; }
  &__title {
    color: $white;
    font-size: 5.6rem;
    font-weight: 500;
    line-height: 1.14;
    padding: 28px 0 12px; }
  &__txt {
    font-size: 2.2rem;
    line-height: 1.45;
    color: $white; } }
