.courses-section {
  padding-top: 74px;
  margin-bottom: 80px; }

.name-section {
  font-size: 1.6rem;
  color: $black;
  font-weight: 700;
  line-height: 2; }

.courses__header {
  @extend %container;
  flex-direction: column; }

.courses {
  @include mediaSize($tablet) {
    width: auto; }
  @include mediaSize($projector) {
    width: 1206px; }
  &__title {
    font-size: 4rem;
    color: $black;
    font-weight: 700;
    line-height: 1.14;
    letter-spacing: 1.4px;
    padding-top: 18px;
    @include mediaSize($phone) {
      font-size: 5rem; }
    @include mediaSize($tablet) {
      font-size: 5.6rem; } }
  &__navBar {
    padding-top: 52px; }
  &__list {
    @extend %container;
    border-radius: 24px;
    background-color: $gray2; }
  &__item {
    border-radius: 24px;
    padding: 12px 0;
    &:hover {
      background-color: $grayHover2; } }
  &__link {
    line-height: 1.2;
    color: $black;
    text-decoration: none;
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    &:hover {
      color: $violet; }
    @include mediaSize($phoneVertical) {
      padding: 0 8px;
      font-size: 1rem; }
    @include mediaSize($phone) {
      padding: 0 15px;
      font-size: 1.8rem; }
    @include mediaSize($tablet) {
      padding: 0 27px; } }
  &__cards {
    display: flex;
    justify-content: center;
    padding: 64px 0 48px;
    flex-wrap: wrap; } }
