.testimonials {
  background-color: $gray2;
  padding: 50px 0;
  @include mediaSize($tablet) {
    background: linear-gradient(to left, $gray2 80%, $white 20%);
    padding: 160px 0 80px 0; } }

.testimonial__name-phone {
  margin: 0 auto;
  font-size: 1.6rem;
  color: $black;
  font-weight: 700;
  line-height: 2;
  text-align: center;
  @include mediaSize($tablet) {
    display: none; } }

.fa-comment-dots {
  color: $white;
  font-size: 7rem;
  @extend .flip-scale-2-ver-right; }

.testimonial {
  @extend %container;
  position: relative;
  flex-wrap: wrap;
  justify-content: center;
  @include mediaSize($tablet) {
    flex-wrap: nowrap;
    justify-content: space-between; }
  &__nameForTablet {
    display: none;
    @include mediaSize($tablet) {
      display: block; } }
  &__content {
    width: 300px;
    text-align: center;
    padding: 0 15px;
    z-index: 10;
    @include mediaSize($phone) {
      width: auto; }
    @include mediaSize($tablet) {
      width: 40%; }
    @include mediaSize($notebook) {
      width: 50%;
      text-align: left;
      padding-left: 30px; }
    @include mediaSize($projector) {
      padding-left: 130px; } }
  &__title {
    font-size: 4rem;
    font-weight: 700;
    line-height: 1.14;
    letter-spacing: 1.4px;
    padding: 18px 0 26px;
    @include mediaSize($phone) {
      font-size: 5rem; }
    @include mediaSize($notebook) {
      font-size: 5.6rem;
      width: 450px; } }
  &__slogan {
    font-size: 2.2rem;
    line-height: 1.45;
    @include mediaSize($tablet); }
  &__txt {
    font-size: 1.8rem;
    line-height: 1.78;
    color: $gray5;
    padding: 34px 0 48px; }
  &__btn {
    display: none;
    justify-content: space-between;
    width: 516px;
    padding: 32px 0 0 410px;
    @include mediaSize($tablet) {
      display: flex; } } }

.testimonial-card {
  display: none;
  width: 300px;
  height: 560px;
  border-radius: 40px;
  margin-top: 50px;
  padding: 48px;
  @extend .shadow-drop-2-lr;
  @include mediaSize($phone) {
    width: 100%; }
  @include mediaSize($tablet) {
    width: 400px;
    margin-top: 0; }
  @include mediaSize($notebook) {
    width: 500px; }
  @include mediaSize($desktop) {
    width: 540px; }
  @include mediaSize($projector) {
    width: 570px; }
  &__txt {
    height: 260px;
    font-size: 1.8rem;
    line-height: 1.78;
    color: $white;
    margin: 20px 0;
    overflow-y: scroll;
    @include mediaSize($desktop) {
      height: auto;
      padding: 40px 0 35px;
      margin: 0;
      overflow-y: hidden; } }
  &__header {
    display: flex;
    align-items: center; }
  &__img {
    width: 96px;
    height: 96px;
    border-radius: 50%;
    background-size: cover;
    background-position: center; }
  &__author {
    padding-left: 24px; }
  &__name {
    font-size: 2.4rem;
    line-height: 1.33;
    color: $white; }
  &__position {
    font-size: 1.8rem;
    line-height: 1.78;
    color: $gray7; } }
